.menu{
    ul{
        font-size: 0;
        li{
            height: 24px;
            line-height: 24px;
            display: inline-block;
            @include fontstyle($f12,$fw5,$color-gray-a);
            text-align: center;
            cursor: pointer;
        }
    }
    .menu-click{
        background-color: $color-blue-p;
        border-radius: 16px;
        color: $color-white;
    }
}