.main{
    height: 100vh;
    background-color: $color-gray-f;
    padding-top:35px; 
    .back{
        margin-left: 1.5625%;
    }
    .title{
        margin-left: 2.34375%;
        @include fontstyle($f18,$fw9,$color-gray-3e);
    }
    .gruop{
        margin-left: 2.34375%;
        margin-right: 2.34375%;
        ul{
            font-size: 0;
            li{
                display: inline-block;
                width: 156px;
                height: 60px;
                padding-left: 5px;
                padding-top: 10px;
                margin-bottom: 16px;
                margin-left: 4px;
                background-color: $color-blue-51;
                border-radius: 4px;
                box-shadow: 0 1px 8px 0 rgba(0, 0, 0, 0.35);
            }
        }
        h3{
            @include fontstyle($f16,$fw9,$color-white);
        }
        p{
            margin: 0;
            @include fontstyle($f16,$fwb,$color-yellow-ff);
        }
    }
}