.setting-header{
    height: 84px;
   top: 0;
   -webkit-backdrop-filter: blur(10px);
   backdrop-filter: blur(10px);
   background-color: $color-white;
   z-index: 2;
   .icon-modify{
       height: 100%;
       position: relative;
       h2{
        @include fontstyle($f32,$fw9,$color-black);
        text-align: center;
        padding-top: 27px;
       }
        .back{
        position: absolute;
        top: 36px;
        left: 16px;
        }
   }  
}
.sidebar{
    width: 5.46875%;
    height: 684px;
   background-color:$color-blue-p;
   padding-top: 56px;
   float: left;
   .icornbox{
        width: 100%;
        height: 56px;
        line-height: 56px;  
        text-align: center;
        font-size: 0;
        &.this{
            background-color:$color-gray-f;
        }
        .icorn{
            width: 24px;
            height: 24px;
        }
        .icorn-1{
            background-image: url("../images/install-24-grey.svg");
            background-size: cover;
            background-repeat: no-repeat;
            &.this{
                background-image: url("../images/install-24.svg");
            }
        }
        .icorn-2{
            background-image: url("../images/manage-24-grey.svg");
            background-size: cover;
            background-repeat: no-repeat;
            &.this{
                background-image: url("../images/manage-24.svg");
            }
        }
        .icorn-3{
            background-image: url("../images/default-terminal-24-grey.svg");
            background-size: cover;
            background-repeat: no-repeat;
            &.this{
                background-image: url("../images/default-terminal-24.svg");
            }
        }
        .icorn-4{
            background-image: url("../images/about-licence-24-white.svg");
            background-size: cover;
            background-repeat: no-repeat;
            &.this{
                background-image: url("../images/about-licence-24.svg");
            }
        }
   }
}
.setting-container{
    width: 94.53125%;
    height: 684px;
    background-color:$color-gray-f;
    float: left;
    position: relative;
    .base{
        margin:12px 0 12px 1.2396942%;
        width: 31.818182%;
        height: 660px;
        border-radius: 4px;
        float: left;
        background-color:$color-white;
        .title{
            width: 84.4155844%;
            margin: auto; 
            margin-bottom: 24px;
            @include fontstyle($f12,$fwb,$color-black-2);
            &.mt-48{
                margin-top: 48px;
            }
        }
        .rectangle-4{
            width: 84.4155844%;
            min-width: 260px;
            height: 56px;
            margin: auto;
            padding: 10px 0;
            margin-bottom: 16px;
            // padding-left:7.79220779%;
            padding-left: 4.61538462%;
            border-radius: 4px;
            box-shadow: 0 1px 0 0 $color-black-2;
            border: solid 1px $color-blue-p;
            background-color: $color-white;
            position: relative;
            letter-spacing: 0;
            word-spacing: 0;
            &.orange{
                padding-top: 0;
                padding-bottom: 0;
                background-color: $color-orange-f;
                box-shadow: none;
                border: 0;
                line-height: 56px;
                h4{
                    color: $color-white;
                    font-style: italic;
                }
            }
            h4{
                @include fontstyle($f14,$fw9,$color-blue-p);
            }
            p{
                @include fontstyle($f10,$fwn,$color-blue-p);
                opacity: 0.7;
            }
            .install,.update{
                width: 48px;
                height: 48px;
                position: absolute;
                top: 8px;
                right: 0;
            }
            .install{
               background-image: url("../images/firmware-install-32.svg");
                background-size: cover;
                background-repeat: no-repeat; 
            }
            .update{
                background-image: url("../images/database-update-32.svg");
                background-size: cover;
                background-repeat: no-repeat; 
            }
        }
        .rectangle-2{
            padding: 0;
            padding-left:7.79220779%;
            line-height: 56px; 
            border: solid 1px $color-gray-a;
            box-shadow: none;
            &.this{
                background-color: $color-blue-p;
                border-color: transparent;
                h4{
                    color: $color-white;
                }
            }    
        }
    }
    .mask-1{
        float: left;
        width: 63.6363387%;
        min-width: 616px;
        height: 660px;
        overflow-y: auto;
        margin: 12px 1.65289256%;
    }
    .mask-2{
        width: 47.008547%;
        height: 162px;
        float: left;
        .contentbox-s-2{
            height: 162px;
            margin: 0;
            background-color: $color-white;
            border-radius: 4px; 
            box-shadow: 1px 1px 4px 0 rgba(0, 0, 0, 0.2), 1px 1px 4px 0 rgba(0, 0, 0, 0.1);
            text-align: center;
            padding: 16px 4.08163265%;
            box-sizing: border-box;
            ul{
                text-align: left;
                margin-top: 16px;
            }
            a{
                text-decoration: underline !important;
            }
            h3{
                @include fontstyle($f16,$fw5,$color-black-2);
            }
            p{
                @include fontstyle($f16,$fw5,$color-gray-a);
                margin-bottom: 0;
                &.blue{
                    color: $color-blue-p;
                }
            }
            .line{
                width: 81.6326531%;
                height: 4px;
                border-radius: 2px;
                background-color: $color-gray-a;
                position: relative;
                margin: 16px auto 8px auto;
                &:before{
                    content: '';
                    width: 56.875%;
                    height: 4px;
                    border-radius: 2px;
                    background-color: $color-orange-f;
                    position: absolute;
                    top:0;
                    left: 0;
                }
            }
            .done{
                width: 64px;
                height: 64px;
                margin:auto;
                margin-top: 14px;
                background-image: url("../images/done-24.svg");
                background-size: cover;
                background-repeat: no-repeat; 
            }
            .btn-box{
                background-image: none;
            }
        }
    }
    .btn-box{
        margin-bottom: 0;
        padding: 24px 0 12px 0;
        background-image: linear-gradient(to bottom, rgba(249, 249, 250, 0), $color-gray-f);
    }
    .contentbox-s{
        width: 63.6363636%;
        margin: 0 auto;
        margin-top: 48px;
        background-color: $color-white;
        border-radius: 4px; 
        box-shadow: 1px 1px 4px 0 rgba(0, 0, 0, 0.2), 1px 1px 4px 0 rgba(0, 0, 0, 0.1);
        text-align: center;
        padding: 16px 4.08163265%;
        box-sizing: border-box;
        ul{
            text-align: left;
            margin-top: 16px;
        }
        a{
            text-decoration: underline !important;
        }
        p{
            @include fontstyle($f16,$fw5,$color-gray-a);
            margin-bottom: 0;
        }
        .line{
            width: 81.6326531%;
            height: 4px;
            border-radius: 2px;
            background-color: $color-gray-a;
            position: relative;
            margin: 16px auto 8px auto;
            &:before{
                content: '';
                width: 56.875%;
                height: 4px;
                border-radius: 2px;
                background-color: $color-orange-f;
                position: absolute;
                top:0;
                left: 0;
            }
        }
        .btn-box{
            background-image: none;
        }
    }
    .setting-sidebar{
        width: 100%;
        border-radius: 4px;
        background-color: $color-white;
        padding:24px 0; 
        box-shadow: 1px 1px 4px 0 rgba(0, 0, 0, 0.2);
