            @include fontstyle($f16,$fw5,$color-black-2);
        }
        .inline-block{
            width: 50%;
        }
        .slideline2{
            text-align: right;
            ul{
                width: 53.5211268%;
                display: inline-block;
            }
        }
        .input-style{
            width: 53.6971831%;
        }
        .checkbox-new{
            margin: 0;
            label{
                margin-bottom: 0;
                height: 31px;
                line-height: 31px;
            }
        }
        .addnew-btn{
            width: 14.9647887%;
        }
        .schedule-btn-2{
            width: 54.0477816%;
        }
        .clear-btn{
            width: 54.0477816%;
        }
    }
    .setting-sidebar{
        width: 100%;
        border-radius: 4px;
        background-color: $color-white;
        padding: 24px 0;
        .select-style-2,.select-style-3{
            float: left;
            width: 44.8051948%;
