.p48percent{
    margin-left: 4.83870968%;
    margin-right: 4.83870968%;
}
.dashboard{
    width: 291px;
    display: inline-block;
    margin-bottom: 20px;
    cursor: pointer;
    .imgbox{
        width: 291px;
        height: 166px;
        background-color: $color-gray-e;
        img{
            width: 100%;
            height: 100%;
            vertical-align: bottom;
        }
    }
    p{
        text-align: center;
        @include fontstyle($f14,$fwb,$color-black-2);
        height: 20px;
        line-height: 20px;
        background-color: $color-gray-d2;
        margin: 0;
    }

}
.smallbar{
    ul{
        font-size: 0;
        text-align: center;
        li{
            display: inline-block;
            background-color: $color-gray-e;
            cursor: pointer;
            margin-left: 2px;
        }
    }
    .imgbox{
        width: 115px;
        height: 65px;
        img{
            width: 100%;
            height: 100%;
            vertical-align: bottom;
        }
    }
    .click{
        border: 4px solid $color-blue-p;
        border-radius: 4px;
    }
}
.bigbox{
    width: 848px;
    margin: auto;
    margin-top: 24px;
    position: relative;
    .imgbox{
        width: 848px;
        height: 484px;
        img{
            width: 100%;
            height: 100%;
            vertical-align: bottom;
        }
    }
    p{
        text-align: center;
        @include fontstyle($f14,$fwb,$color-black-2);
        height: 20px;
        line-height: 20px;
        background-color: $color-gray-d2;
        margin: 0;
    }
    .screen-resize{
        width: 32px;
        height: 32px;
        background-image: url("../images/screen-resize-nor.svg");
        background-size: cover;
        position: absolute;
        right:-48px;
        &.center{
            top: 50%;
            transform: translateY(-50%);
        }
        &.down{
            bottom:42px;
        }
    }
    .gallery-view-mode{
        width: 32px;
        height: 32px;
        background-image: url("../images/gallery-view-mode-nor.svg");
        background-size: cover;
        position: absolute;
        right:-48px;
        &.center{
            top: 59%;
            transform: translateY(-59%);
        }
        &.down{
            bottom:0;
        }
    }
}