.lightbox_bg{
    position: fixed;
    left: 0;
    top:0;
    z-index: 3;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.5);
    margin: auto;
    &.all{
        z-index: 4;
    }
    &.zindex2{
        z-index: 2;
    }
    
}
.lightbox_bg-2{
    position: fixed;
    top:0;
    width: 100%;
    height: 100%;
    margin: auto;
    z-index: 4;
        background-color:rgba(255,255,255,0.8);
        .spinner{
            width: 35px;
            height: 35px;
            position: absolute;
            transform: translate(-50%,-50%);
            left: 50%;
            top: 50%;
            background-image: url("../images/dark-indeterminate-spinner@2x.png");
            background-size: cover;
            background-repeat: no-repeat;
            animation-name: loding;
            animation-duration: 1.8s;
            animation-iteration-count: infinite;
       }
       @keyframes loding {
        0%   { transform: rotate(0deg)}
        5%  { transform: rotate(18deg)}
        10%  { transform: rotate(36deg)}
        15%  { transform: rotate(54deg)}
        20%  { transform: rotate(72deg)}
        25%  { transform: rotate(90deg)}
        30%  { transform: rotate(108deg)}
        35%  { transform: rotate(126deg)}
        40%  { transform: rotate(144deg)}
        45%  { transform: rotate(162deg)}
        50%  { transform: rotate(180deg)}
        55%  { transform: rotate(198deg)}
        60%  { transform: rotate(216deg)}
        65%  { transform: rotate(234deg)}
        70% { transform: rotate(252deg)}
        75% { transform: rotate(270deg)}
        80%  { transform: rotate(288deg)}
        85%  { transform: rotate(306deg)}
        90%  { transform: rotate(324deg)}
        95%  { transform: rotate(342deg)}
        100% { transform: rotate(360deg)}
      }
}
