// bryan

.w_container {
    margin: 0 auto;
    width: 1024px;
    
}



@include respond-to(medium) {
    .modal-dialog {
        width: 1440px;
      }
    .w_container {
        margin: 0 auto;
        width: 1440px;
    }
}

@include respond-to(large) {
    .w_container {
        margin: 0 auto;
        width: 1920px;
    }

    .modal-dialog {
        width: 1920px;
      }

    .groupbox .files {
        width: 156px;
        height: 81px;
        margin: 1px 1px 3px;
        float: left;
        position: relative;
    }


    .groupbox {
        overflow-x: auto;
        height: 186px;


    }

}

.w_container992 {
    margin: 0 auto;
    width: 992px;
}

@include respond-to(medium) {
    .w_container992 {
        margin: 0 auto;
        width: 1395px;
    }
}

@include respond-to(large) {
    .w_container992 {
        margin: 0 auto;
        width: 1860px;
    }
}

//解決modal 導圓角的bug
.bgmask {
    background-color: #7e7e7e;
    height: 56px;
    position: sticky;
    z-index: 2;
    top: 0;
    width: 100%;
    margin-bottom: -56px;
}