.combined-shape{
    box-sizing: border-box;
    padding: 0 2.34375%;
    background-color: $color-gray-f;
    z-index: 3;
    .searchgroups{
        text-align: right;
    }
    .groupcontainer{
        width: 100%;
    }
    .groupbox{
        height: 90px;
    }
}
.slideup{
    position: relative;
    transition: margin-top 2s;
    margin-top: -200px !important;
}
.slidedown{
    margin-top: 0 !important;
}
.quick-switch-img{
    width: 100px;
    height: 38px;
    background: url(../images/quick-switch-arrow-down.svg);
    background-size: cover;
    position: absolute;
    left: 45%;
    z-index: 5;
    cursor: pointer;
}
.quick-switch-img-up{
    background: url(../images/quick-switch-arrow-up-close.svg);
}
.menu{
    position: -webkit-sticky;
    position: sticky;
    z-index: 2;
    background-color: $color-gray-f;
    top: 56px;
    height: 56px;
    line-height: 65px;
}
