            }
            .up,.down{
                position: absolute;
                width: 20px;
                height: 20px;
                background-image: url("../images/dropdown-24.svg");
                background-size: cover;
                background-repeat: no-repeat;
                cursor: pointer;
            }
            .up{
                top: -5px;
                left:-5px;
                transform: rotate(180deg);
            }
            .down{
                top: 5px;
                left:-5px;
            }
        }
