                .w-600 {
                    width: 1125px;
                }
            }
            .bor-left{
                position: relative;
                padding-left: 8px;
                &:before{
                    content: '';
                    height: 80px;
                    border-left: 2px solid $color-gray-d8;
                    position: absolute;
                    left: 0;
                    top: 0;
                }
            }
            .bor-left2{
                position: relative;
                padding-left: 18px;
                padding-top: 11px;
                &:before{
                    content: '';
                    height: 80px;
                    border-left: 2px solid $color-gray-d8;
                    position: absolute;
                    left: 0;
                    top: 0;
                }
                &:after{
                    content: '';
                    height: 80px;
                    border-left: 2px solid $color-gray-d8;
                    position: absolute;
                    left: 8px;
                    top: 0;
                }
            }
            .clear-btn{
                position: relative;
                top: -5px;
                width: 45.6354986%;
                min-width: 141px;
                font-size: 12px;
            }
