            @include fontstyle($f12,$fw5,$color-gray-a);
        }
        .title{
            float: left;
            height: 33px;
            line-height: 33px;
            border-top:1px solid $color-gray-a;
            border-bottom:1px solid $color-gray-a;
        }
        .title-left{
            width: 31.041666%;
            min-width: 298px;
        }
        .title-center{
            width: 17.9166667%;
