    }
    .select-content{
        width: 48.958333%;
        min-width: 470px;
        background-color: $color-white;
        .click{
            background-color: $color-blue-p;
            color: $color-white;
            position: relative;
            &.border-bottom{
                &:after{
                    contain: '';
                    border-bottom:0; 
                }
            }
            &:before{
                content: '‣';
                color: $color-white;
                font-size: $f20;
                position: absolute;
                top:0;
                right:8px;
            }
        }
        p{
            float: left;
            padding-left: 3.403455%;
        }
        .left{
            width: 63.402553%;
            min-width: 298px;
        }
        .right{
            width: 36.595744%;
            min-width: 172px;
        }
        .border-bottom{
            position: relative;
            &:after{
                content: '';
                width: 93.191184%;
                min-width: 438px;
                border-bottom: 1px solid $color-gray-d0;
                margin-left: 3.40345532%;
                position: absolute;
                bottom: 0;
                left:0;


            }
        }
    }
    .select-content2{
        width: 51.041666%;
        min-width: 490px;
        p{
            padding-left: 3.265306%;
        }
        li{
            position: relative;
            &:after{
                content: '';
                width: 5px;
                height: 36px;
                background-color: $color-blue-p;
                position: absolute;
                top:0;
                left:0;
            }
        }
    }
}

