.btn-box{
    text-align: right;
    margin-bottom: 24px;
    &.position{
        position: absolute;
        right:24px;
        bottom:24px;
    }
    &.left{
        text-align: left;
    }
    .btn-back-finish{
        width: 84px;
        height: 32px;
        @include fontstyle($f16,$fwb,$color-blue-p);
        border:2px solid $color-blue-p;
        border-radius: 4px;
        margin-left: 24px;
        background-color: $color-white;
        box-shadow: 0 1px 10px 0 rgba(0, 0, 0, 0.2);
        cursor: pointer;
        &.bluebg{
            background-color: $color-blue-p;
            color: $color-white;
            
        }
        &.w-140{
            width: 140px;
        }
        &.w-a{
            width: auto;
        }
        &.transparent{
            background-color: transparent;
            border: none;
            box-shadow: none;
        }
    }
    .btn-back-finish-2{
        width: 36.5187713%;
        &.ml-40{
            margin-left: 40px;
        }
    }
}

.btn_style{
    text-align: right;
    @include fontstyle(0); 
    word-spacing:0px;
    a{
        @include fontstyle($f16,$fwb,$color-blue-p);
        width: 76px;
        height: 32px;
        line-height: 32px;
        display: inline-block;
        text-align: center;
        margin-left: 24px;
        cursor: pointer;
    }
}


.addnew-btn{
    @include fontstyle($f12,$fwb,$color-blue-p);
    width: 86px;
    height: 28px;
    line-height: 28px;
    text-align: center;
    border:1px solid $color-blue-p;
    border-radius: 4px;
    cursor: pointer;
}

.schedule-btn{
    position: relative;
    width: 46.212121%;
    min-width: 305px;
   padding-top: 6px;
   padding-left: 4.54545455%;
   padding-right: 2.72727273%;
   line-height: 1.3;
   border-radius: 4px;
    box-shadow: 1px 1px 4px 0 rgba(0, 0, 0, 0.2);
    @include fontstyle($f14,$fw5,$color-blue-p);
   &::after{
       display: block;
       width: 16px;
       height: 16px;
       content: '';
       position: absolute;
       top: 50%;
       transform: translateY(-50%);
       left: 1.96721311%;
       background-image: url("../images/black-16.svg");
       background-size: cover;
       background-repeat: no-repeat;
   }
   &.select{
    background-color: $color-blue-p;
    color: $color-white;
}
   p{
       margin: 0;
   }
   .modify-del-img{
        display: block;
       width: 28px;
       height: 28px;
       position: absolute;
       top: 50%;
       transform: translateY(-50%);
       right: -48px;
       background-image: url("../images/28-black.svg");
       background-size: cover;
       background-repeat: no-repeat;

   }
}

