.slideline,.slideline2,.slideline3{
    // margin-bottom: 24px;
    ul{
        word-spacing: 0;
        letter-spacing: 0;
        font-size: 0;
        height: 32px;
        line-height: 32px;
        background-color: $color-gray-e;
        border-radius: 16px;
        padding: 0 2px;
        box-sizing: border-box;
        li{ 
            text-align: center;
            display: inline-block;
            @include fontstyle($f12,$fw5,$color-gray-a);
            cursor: pointer;
        }
    }
    .bg-white{
        height: 28px;
        line-height: 26px;
        background-color: $color-white;
        border-radius: 14px;
        @include fontstyle($f14,$fwb,$color-blue-p);
    }
}
.slideline{
    li{
        width: 46.8401487%;
    }
    .bg-white{
        width: 53.1598513%;
    }
}
.slideline2{
    li{
        width: 47.3684211%;
    }
    .bg-white{
        width: 52.6315789%;
    }
    span{
        @include fontstyle($f14,$fwn,$color-gray-a);
    }
}
.slideline3{
    width: 25%;
    text-align: right;
    ul{
        width: 62.3%;
    }
    li{
        width: 47.3684211%;
    }
    .bg-white{
        width: 52.6315789%;
    }
    span{
        @include fontstyle($f14,$fwn,$color-gray-a);
    }
}

