.btn-bar{
    margin: 16px 0;
    font-size: 0;
    &:after{
        content: '';
        display: block;
        width: 100%;
        height: 0;
        clear: both;
    }
    .bar{
        // width: 156px;
        display: inline-block;
    }
    .bor-rt-a{
        width: 1px;
        height: 18px;
        display: inline-block;
        background-color: $color-gray-a;
        margin: 0 7px; 
        margin-bottom: 3px;
    }
    .bar-2{
        width: 170px;
        display: inline-block;
    }
    .bar1{
        float: left;
        @include fontstyle($f12,$fwb,$color-blue-p);
        width: 68px;
        height: 24px;
        margin: 0 2px;
        line-height: 24px;
        border:1px solid $color-gray-a;
        border-radius: 16px;
        text-align: center;
        position: relative;
        cursor: pointer;
        letter-spacing: 0;
        word-spacing: 0;
        &:hover{
            color:$color-white;
            background-color: $color-blue-p;
            border-color:$color-blue-p; 
        }
        &.bar-click{
            color:$color-white;
            background-color: $color-blue-p;
            border-color:$color-blue-p; 
        }
        &.w-122{
            width: 122px;
        }
        .img-star{
            width: 18px;
            height: 18px;
            background: url("../images/favorited-18.svg");
            background-size: cover;
            background-repeat: no-repeat;
            position: absolute;
            top:50%;
            left:50%;
            transform: translate(-50%,-50%);
        }
    }
 .bar .bar1:nth-child(2){
       margin-right: 4px;;
    }
    .bar2{
        width: 80px;
        margin-left: 8px;
    }
    .bar3{
        width: auto;
        padding: 0 10px;
    }
}

.count-btn{
    font-size: 0;
    p{
        display: inline-block;
        @include fontstyle($f16,$fw5,$color-gray-4);
        width: 59px;
        text-align: center;
    }
    .countbtn{
        display: inline-block;
        width: 32px;
        height: 32px;
        line-height: 32px;
        background-color: $color-gray-eb;
        text-align: center;
        @include fontstyle($f18,$fwb,$color-blue-p);
        border-radius: 50%;
        cursor: pointer;
    }
}