.editor-header{
    background-color: $color-blue-0;
    height: 56px;
    line-height: 56px;
    position: -webkit-sticky;
    position: sticky;
    z-index: 3;
    top:0;
    &.orange{
        background-color: $color-orange-f;
    }
    &.green{
        background-color: $color-green-2;
    }
    &.black-4{
        background-color: $color-gray-4;
    }
    .title{
        float: left;
        padding-left: 16px;
        @include fontstyle($f18,$fw9,$color-white);
        .surper-admin{
            position: relative;
            display: inline-block;
            width: 20px;
            height: 20px;
           margin-right: 8px;
            background-image: url("../images/super-admin-white-20.svg");
            background-size: cover;
            background-repeat: no-repeat; 
            top: 4px; 
        }
    }
}
.header-type-1{
    background-color: $color-gray-e;
    position: relative;
    .rectangle-2{
        width: 400px;
        font-size: 0;
        .rectangle-3{
            width: 170px;
            height: 40px;
            border-radius: 6px;
            border: 1px solid $color-gray-a;
            text-align: center;
            float: left;
            p{
                margin: 0;  
            }
            .current{
               @include fontstyle($f10,$fwn,$color-gray-a);
               padding-top: 4px;
           }
           .windows{
               @include fontstyle($f12,$fw5,$color-blue-p);
                    } 
        }
        
    }
   .tab,.tab-2,.tab-3{
    height: 40px;   
    float: left;
    text-align: center;
    line-height: 40px;
    cursor: pointer; 
   } 
    .tab{
        width: 25%;  
         @include fontstyle($f18,$fw5,$color-gray-a);
         &.thistab{
         border-top: 2px solid $color-blue-p;
         background-color: $color-gray-f;
          color: $color-blue-p;
           font-weight: 900; 
        }
     }
    .tab-2,.tab-3{
        @include fontstyle($f14,$fw9,$color-gray-a);
         background-color: $color-gray-f;
         &.thistab{
            color: $color-blue-p;
         }
    }
    .tab-2{
        width: 33.333333%;
        .line{
            width: 32.748538%;
            height: 2px;
            margin: auto;
            background-color: $color-blue-p;  
            position: relative;
            top: -2px;  
        }
    }
    .tab-3{
        width: 20%;
        .line{
            width: 59.8039216%;
            height: 2px;
            margin: auto;
            background-color: $color-blue-p;  
            position: relative;
            top: -2px;  
        }
    }
}
.icorn{
    width: 32px;
    height: 32px;
    display: inline-block;
    vertical-align: middle;
    cursor: pointer;
}
.refresh-nor{
    background-image: url("../images/refresh-nor.svg");
    background-size: cover;
    background-repeat: no-repeat;
 }
 .superadminbox{
    width: 66px;
    height: 32px;
    padding: 6px 10px;
    border-radius: 16px;
    background-color: $color-gray-4;
    display: inline-block;
    font-size: 0;
    vertical-align: middle;
    position: relative;
    .pop-up_window-2{
        width: 256px;
        height: 80px;
        background-color: #46423a;
        position: absolute;
        border-radius: 4px;
        top: 38px;
        left: -190px;
        z-index: 3;
        .showbox{
            &:before{
                content: '';
                width: 0;
                height: 0;
                border-style: solid;
                border-width: 5px;
                border-color: #46423a #46423a transparent transparent;
                transform: rotate(-45deg);
                position: absolute;
                top:-4px;
                left: 225px;
            }
            li{
                height: 40px;
                line-height: 40px;
                padding-left: 36px;
                @include fontstyle($f14,$fw9,$color-white);
                position: relative;
                .logout,.account-management{
                    width: 20px;
                    height: 20px;
                    position: absolute;
                    top: 10px;
                    left: 8px;
                }
                .logout{
                    background-image: url("../images/logout-white-24.svg");
                    background-size: cover;
                }
                .account-management{
                    background-image: url("../images/account-management-24.svg");
                    background-size: cover;
                }
            }
            .bg-r{
                background-color: $color-red-e;
            }
            .last{
                border-radius: 0 0 4px 4px;
            }
        }
    }
    .super-admin-white-20{
        width: 20px;
        height: 20px;
        background-image: url("../images/super-admin-white-20.svg");
        background-size: cover;
        background-repeat: no-repeat;
    }
    .dropdown-white-24{
        width: 20px;
        height: 20px;
        background-image: url("../images/dropdown-white-24.svg");
        background-size: cover;
        background-repeat: no-repeat;
        &.up-img{
            background-image: url("../images/dropdown-close-white-24.svg");
        }
    }
}
