.guild-content{
    position: relative;
    height: 32px;
    line-height: 32px;
    @include fontstyle($f12,$fw5,$color-black-2);
    padding-left:8px;
    box-sizing: border-box; 
    border: 1px solid $color-gray-a;
    border-radius: 4px;
    p{
        margin: 0;
    }
    .del-icorn{
        width: 16px;
        height: 16px;
        padding-top: 8px;
        padding-left: 8px;
        display: inline-block;
        background-image: url("../images/16.svg");
        background-size: cover;
        background-repeat: no-repeat;
        vertical-align: middle;
    }
    .btn_style{
        position: absolute;
        height: 24px;
        line-height: 24px;
        text-align: center;
        @include fontstyle($f12,$fwb,$color-blue-p);
        border:1px solid $color-blue-p;
        border-radius: 4px;
        top:3px;
        right:4px;
        cursor: pointer;
    }
}