//template.html 使用
.portlet {
    float: left;
    background-color: $color-white;
    box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, .2);
    width: 800px;
    margin: 20px;
    padding: 10px;

    h2 {
        font-size: 36px;
    }
}
h2,h3,ul,h4{
   margin-top: 0;
   margin-bottom: 0;
}
//常用的寫法
a {
    text-decoration: none !important;
}

.clearfix {
    clear: both;

    &:after {
        content: '';
        display: block;
        height: 0;
        clear: both;
    }
}
input{
    outline:none;
}
.wrapper {
    width: 100%;
    min-width: 1024px;
    min-height: 768px;
    @include margin-auto();
}

.wrapper2 {
    width: 96.875%;
    min-width: 992px;
    height: 100%;
    min-height: 768px;
    margin: auto;
    background-color: $color-gray-f;
    position: relative;
    z-index: 2;
    letter-spacing: 0; 
    word-spacing:0; 
    &.mt-84 {
        margin-top: 84px;
    }
}
.title-msg{
    height: 68px;
    line-height: 68px;
    font-size: 24px;
    font-weight: $fwb;
    color: $color-white;
    text-align: center;
}
.wrapper3{
    min-width: 994px;
    border: solid 1px rgba(255, 255, 255, 0.27);
  background-color: rgba(255, 255, 255, 0.27);
}
.wrap-960 {
    min-width: 960px;
    width: 96.774193%;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 12px;
    box-sizing: border-box;
    position: relative;
    padding-left: 1.61290323%;
    padding-right: 1.61290323%;
}



.wrap-bg-w {
    background-color: $color-white;
    border-radius: 6px;
}
.wrap-bg-f4{
    background-color: $color-gray-f4;
    border-radius: 6px;
}
.w100percent {
    width: 100%;
}

.w33percent {
    width: 33.333333%;
}

.bold {
    font-weight: bold;
}

.none {
    display: none;
}

.float_left {
    float: left;
}

.float_right {
    float: right;
}

.mh-768 {
    min-height: 768px;
}

.mh-700 {
    min-height: 700px;
}

