.groupcontainer {
    width: 95.3125%;
    margin: 0 auto;
    &.w96percent{
        width: 96%;
    }
}

.searchcontainer h2 {
    @include fontstyle($f18, $fw9, $color-black-2);
    
}
.searchcontainer .box-btn{
        background-color:transparent;
        height: 32px;
        .up,.down{
            position: absolute;
            width: 20px;
            height: 20px;
            left: 0;
            background-image: url("../images/dropdown-24.svg");
            background-size: cover;
            background-repeat: no-repeat;
            cursor: pointer;
        }
        .up{
            top: -5px;
            transform: rotate(180deg);
        }
        .down{
            top: 5px;
        }
    }
.searchgroups {
    font-size: 0;
    text-align: right;
    width: 50%;
    .search{
        width: 65.5737705%;
    }
}

%borders {
    border-radius: 4px;
    box-shadow: 0 1px 8px 0 rgba(0, 0, 0, 0.25);
}

.groupbox {
    overflow-x: auto;
    height: 180px;
    position: relative;
    .bg-orange {
        // background-color: $color-orange-f;
        background-image: url("../images/ternimal-active@2x.png");
        background-size: cover;
        background-repeat: no-repeat;
        
    }
    &.h-300{
        height: 300px;
    }
    .bg-blcak {
        background-image: url("../images/card-terminal-off@2x.png");
        background-size: cover;
        background-repeat: no-repeat;
        
    }

    .bg-yellow {
        background-image: url("../images/ternimal-booting@2x.png");
        background-size: cover;
        background-repeat: no-repeat;
    }

    .bg-gray {
        background-image: url("../images/ternimal-disable@2x.png");
        background-size: cover;
        background-repeat: no-repeat;
    }
    .bg-green{
        background-image: url("../images/rds@2x.png");
        background-size: cover;
        background-repeat: no-repeat;
    }
    .group {
        background-image: url("../images/files_bg.png");
        background-size: cover;
        background-repeat: no-repeat;
    }

    .files {
        width: 156px;
        height: 80px;
        margin: 1px 2px 4px;
        float: left;
        position: relative;
        .checkbox-new{
            top: 12px;
        }
        .box {
            width: 100%;
            height: 90%;
        }

        .img-star {
            width: 20px;
            height: 20px;
            background-image: url(../images/favorited-badge-18.svg);
            background-size: cover;
            background-repeat: no-repeat;
            position: absolute;
            top: -3px;
            right: 0;
        }
        p {
            width: 128px;
            height: 38px;
            margin-top: 10px;
            margin-left: 12px;
            @include fontstyle($f14, $fwb, $color-white);
            letter-spacing: 0;
            word-spacing: 0;
            word-break: break-all;
        }

        .img-lock {
            width: 16px;
            height: 16px;
            background-image: url("../images/locked-16-hover.svg");
            background-position: 0px;
            background-size: cover;
            background-repeat: no-repeat;
            position: absolute;
            bottom: 10px;
            left: 12px;
        }

    }

    .files2 {
        width: 156px;
        height: 80px;
        margin: 1px 2px 4px;
        padding-left: 6px;
        float: left;
        position: relative;

        .box {
            width: 100%;
            height: 100%;
            padding-right: 14px;
        }

        .img-star {
            width: 20px;
            height: 20px;
            background-image: url(../images/favorited-badge-18.svg);
            background-size: cover;
            background-repeat: no-repeat;
            position: absolute;
            top: -8px;
            right: -4px;
        }
        .checkbox-new{
            top: 4px;
            right: 2px;
        }
        .img-lock {
            width: 16px;
            height: 16px;
            background-image: url("../images/locked-16-hover.svg");
            background-position: 0px;
            background-size: cover;
            background-repeat: no-repeat;
            position: absolute;
            bottom: 4px;
            left: 6px;
        }

        .status {
            position: absolute;
            margin: 0;
            right: 5px;
            bottom: 2px;
            opacity: .4;
            @include fontstyle($f16, $fw9, $color-white);
            letter-spacing: none;
            &.black{
                color: $color-black;
            }
        }

        .statusblack {
            color: $color-black;
            opacity: .15;
        }

        .black {
            color: $color-black-2;
        }

        p {
            @include fontstyle($f14, $fwb, $color-white);
            line-height: 1.2;
            margin: $m0;
            margin-top: 6px;
            letter-spacing: 0;
            word-spacing:0;
            word-break: break-word;
        }

        .mt {
            margin-top: 20px;
        }

        .group-name {
            opacity: 0.7;
            @include fontstyle($f10, $fw5, $color-white);
            &.black{
                color: $color-black-2;
            }
        }

        .lblack {
            color: $color-black-2;
        }
    }
    .noternimal{
        position: absolute;
        left: 50%;
        top: 45px;
        transform: translateX(-50%);
        @include fontstyle($f18,$fw5,$color-gray-a4);
    }
    .click{
        border: 4px solid $color-blue-p;
        border-radius: 4px;
    }
    .checkbox-new{
        position: absolute;
        margin-bottom: 0;
        right: 4px;
        &.checkbox-2{
            top: 6px;
            right: 0;
            left: 16px;
        }
      .checkbox-style{
        width: 16px;
        height: 16px;
        border-radius: 8px;
        top: 0;
        margin-right: 0;
        outline: none;
        &:checked::before{
            font-size: $f12;
        }
      }
    }
    
}