@mixin FontFamily($fontfamilys) {
    @if $fontfamilys=='cwTeXHei' {
        @import url(//fonts.googleapis.com/earlyaccess/cwtexhei.css);
        body {font-family:'cwTeXHei','Lato',  sans-serif;}
    }
    @else if $fontfamilys =='cwTeXMing'{
        @import url(//fonts.googleapis.com/earlyaccess/cwtexming.css);
        body {font-family:'cwTeXMing', sans-serif; }
    }
    @else if $fontfamilys =='Noto_Sans_TC'{
        @import url(//fonts.googleapis.com/earlyaccess/notosanstc.css);
        body {font-family:'Noto Sans TC', sans-serif; }
    }
    @else if $fontfamilys =='cwTeXFangSong'{
        @import url(//fonts.googleapis.com/earlyaccess/cwtexfangsong.css);
        body {font-family:'cwTeXFangSong', sans-serif;}
    }
    @else if $fontfamilys =='cwTeXYen'{
        @import url(//fonts.googleapis.com/earlyaccess/cwtexfangsong.css);
        body {font-family:'cwTeXYen' , sans-serif;}
    }
    //簡體
    @else if $fontfamilys =='notosansscsliced'{
        @import url(//fonts.googleapis.com/earlyaccess/notosansscsliced.css);
        body {font-family: 'Noto Sans SC Sliced', sans-serif;}
    }
    @else if $fontfamilys =='notosanssc'{
        @import url(//fonts.googleapis.com/earlyaccess/notosanssc.css);
        body {font-family: 'Noto Sans SC', sans-serif;}
    }
    @else if $fontfamilys =='notosanssc'{
        @import url(//fonts.googleapis.com/earlyaccess/notosanssc.css);
        body {font-family: 'Noto Sans SC', sans-serif;}
    }
    @else if $fontfamilys =='muli'{
        @import url(//https://fonts.googleapis.com/css?family=Open+Sans:400,600);
        body {font-family: 'Open Sans', sans-serif;}
    }
    @else if $fontfamilys =='Roboto'{
        // @import url(../font/Roboto-Black.ttf);
        @font-face {
            font-family: Roboto;
            src: url(../font/Roboto-Regular.ttf);
          }
        body {font-family:'Roboto';}
    }
    @else if $fontfamilys =='Font Awesome 5 Free'{
        @import url(/dev/sass/fontawesome/webfonts/fa-brands-400.eot);
        body {font-family: 'Font Awesome 5 Free', sans-serif;}
    }
    
}
