.menubox{
    width: 86.440678%;
    height: 476px;
    border-radius: 6px;
    background-color: $color-white;
    // background-color: orange;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    z-index: 2;
    .centerbox{
        width: 68.627451%;
        margin: auto;
    }
    .showbox{
        width: 100%;
        height: 315px;
        background-image: linear-gradient(to left, $color-gray-7, $color-gray-4f);
    }
    .showbox2{
        width: 100%;
        height: 315px;
        border: 1px solid $color-black-2;
        .bac{
            height: 236px;
            margin-top: 39px;
            background-image: linear-gradient(to left, $color-gray-7, $color-gray-4f);
        }
    }
    .btn_style{
        height: 57px;
        padding-top: 12.5px;
        border-radius: 0 0 6px 6px;
        background-image: linear-gradient(to bottom, $color-gray-f, $color-white);
    }
}
.showbox-style{
    font-size: 0;
    margin-top: 24px;
    .left{
        display: inline-block;
        vertical-align: top;
        width: 63.6363636%;
        height: 315px;
        background-image: linear-gradient(to left, $color-gray-7, $color-gray-4f);
        position: relative;
        .size{
            border: 3px solid $color-blue-p;
            border-radius: 4px;
            background-color:rgba($color: $color-white, $alpha:0.9);
            display: inline-block;
           position: absolute;
            .triangle{
                position: absolute;
                width: 0;
                height: 0;
                border-style: solid;
                border-width: 46px 46px 0 0;
                border-color: $color-blue-p transparent transparent transparent;
                z-index: 2;
                p{
                    width: 20px;
                    height: 20px;
                    text-align: center;
                    position: absolute;
                    top: -47px;
                    left: 2px;
                    font-size: $f14;
                    font-weight:$fw5;
                    color: $color-white;
                    margin: 0;
                }
            }
            .pic{
                width: 199px;
                height: 133px;
                margin-top: 38px;
                img{
                    width: 100%;
                    height: 100%;
                    vertical-align: bottom;
                }
            }
            .pic-2{
                img{
                    width: 100%;
                    &.expand{
                        transform: scale(1.2);
                    }
                }
            }
        }
        .typebox{
            width: 396px;
            margin: auto;
            padding-top: 16px;
            .type{
                width: 190px;
                height: 138px;
                margin: 0 4px;
                margin-bottom: 8px;
                border: 3px solid $color-gray-c8;
                border-radius: 4px;
                background-color:rgba($color: $color-white, $alpha:0.9);
                float: left;
                .triangle{
                    position: absolute;
                    width: 0;
                    height: 0;
                    border-style: solid;
                    &::after{
                        display: block;
                        content: '';
                        width: 54px;
                        height: 2px;
                        position: absolute;
                        background-color: $color-gray-c8;
                        top: 18px;
                        left: -8px;
                        transform: rotate(135deg);
                    }
                   
                    // border-width: 46px 46px 0 0;
                    // border-color: $color-gray-c8 transparent transparent transparent;
                    z-index: 2;
                    p{
                        width: 20px;
                        height: 20px;
                        text-align: center;
                        position: absolute;
                        top: 0;
                        left: 2px;
                        font-size: $f14;
                        font-weight:$fw5;
                        color: $color-blue-p;
                        margin: 0;
                        z-index: 2;
                    }
                    .triangle-2{
                        position: absolute;
                        width: 0;
                        height: 0;
                        left: 0;
                        top: 0;
                        
                        // border-width: 40px 40px 0 0;
                        // border-color: $color-white transparent transparent transparent;
                    }
                }
                &.select{
                    border-color: $color-blue-p;
                    .triangle{
                        &::before{
                            display: block;
                            content: '';
                            width: 0;
                            height: 0;
                            border-style: solid;
                             border-width: 46px 46px 0 0;
                             border-color: $color-blue-p transparent transparent transparent;
                         } 
                         &::after{
                            display: none
                        }      
                        }
                    p{
                        color: $color-white;
                    }
                }
                .pic{
                    width: 184px;
                    height: 123px;
                    margin-top:7px;
                    img{
                        width: 100%;
                        height: 100%;
                        vertical-align: bottom;
                    }
                }
            }
        }
        .type1{
            width: 120px;
            height: 122px;
            top: 12px;
            left: 2.85714286%;
            z-index: 1;
        }
        .type2{
            width: 205px;
            height: 208px;
           top: 12px;
           left: 50%;
           transform: translateX(-50%);
           z-index: 1;
           overflow: hidden;
        }
        .type3{
            width: 205px;
            height: 208px;
           top: 12px;
         left: 108px;
         z-index:0;
         border-color:$color-gray-c8;
         .triangle{
            border-color: $color-gray-c8 transparent transparent transparent;
            p{
                color: $color-blue-p;
                z-index: 2;
            }
            .triangle-2{
                position: absolute;
                width: 0;
                height: 0;
                left: 0;
                top: -46px;
                border-style: solid;
                border-width: 40px 40px 0 0;
                border-color: $color-white transparent transparent transparent;
            }
         }
        }
        .type4{
            z-index: 1;
        }

    }
    .right{
        display: inline-block;
        vertical-align: top;
        width: 36.3636364%;
        height: 315px;
        border-radius: 4px;
        box-shadow: 0 0 15px 0 rgba(0, 85, 150, 0.5);
        position: relative;
        .w-152{
            width: 78.3333333%;
            margin: auto;
            margin-top: 16px;
        }
        .img-line{
            text-align: center;
            padding-top: 16px;
            margin-bottom: 16px;
            .ml-20{
                margin-left: 8.333333%;
            }
            .up{
                display: inline-block;
                width: 37px;
                height: 37px;
                background-image: url("../images/up-32.svg");
                background-size: cover;
                cursor: pointer;
            }
            .down{
                display: inline-block;
                width: 37px;
                height: 37px;
                background-image: url("../images/down-32.svg");
                background-size: cover;
                cursor: pointer;
            }
            .system{
                display: inline-block;
                width: 37px;
                height: 37px;
                background-image: url("../images/system-32.svg");
                background-size: cover;
                cursor: pointer;
            }
            .del{
                display: inline-block;
                width: 37px;
                height: 37px;
                background-image: url("../images/delete-32.svg");
                background-size: cover;
                cursor: pointer;
            }
        }
        ul{
            text-align: center;
            li{
                .font{
                    display: inline-block;
                    width: 70px;
                    text-align: left;
                    @include fontstyle($f12,$fw5,$color-gray-a);
                }
                .count-btn{
                    text-align: right;
                    display: inline-block;
                }
            }
        }
        .btn_style{
            width: 100%;
            border-top: 1px solid $color-gray-d2;
            background-color: $color-white;
            font-size: 0;
            position: absolute;
            left: 0;
            bottom: 0;
            &:after{
                content: '';
                width: 1px;
                height: 30px;
                border-right: 1px solid $color-gray-d2;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%,-50%);
            }
            .botton{
                width: 50%;
                display: inline-block;
                text-align: center;
                padding-top: 4px;
            }
            a{
                margin-left: 0;
            }
        }
        .checkbox-new{
            padding-left: 10%;
            padding-right: 0;
            margin-top: 16px;
            margin-bottom: 8px;
            .radio-style{
                width: unset;
                @include fontstyle($f12,$fw5,$color-gray-4);
                &.ml-12{
                    margin-left: 4.72440945%;
                }
            }
        }
        .addnew-btn{
            margin-left: 10%;
            margin-bottom: 16px;
        }
        .scroll{
            height: 175px;
            overflow-y: auto;
            margin-left: 10%;
            .schedule-btn-2{
                width: 65%;
                min-width: unset;
                padding-left: 11.71875%;
                padding-right: 0;
                margin-bottom: 8px;
            }

        }
        .tab{
           .font{
               display: inline-block;
               vertical-align: top;
               width: 50%;
               height: 32px;
               line-height: 32px;
               text-align: center;
               @include fontstyle($f14,$fwb,$color-gray-a);
               background-color: $color-gray-e6;
               &.click{
                 border-top: 2px solid $color-blue-p;
                 background-color: $color-white;
                 color: $color-blue-p;
               }
           }
        }
    }
}
.wrap01{
    .bottom{
        height: 128px;
        padding: 24px 6.19047619%;
        .circle{
            display: inline-block;
            vertical-align: top;
            width: 32px;
            height: 32px;
            text-align: center;
            line-height: 32px;
            border-radius: 50%;
            @include fontstyle($f14,$fw5,$color-white);
            background-color: $color-gray-d2;
            cursor: pointer;
            &.plusimg{
                position: relative;
                &:after{
                    content: '';
                    width: 40px;
                    height: 40px;
                    background-image: url("../images/plus.svg");
                    background-size: cover;
                    background-repeat: no-repeat;
                    position: absolute;
                    top:-3px;
                    left: -3px;
                }
            }
            &.bgc-blue{
                background-color: $color-blue-p;
            }
            &.bgc-white{
                background-color: $color-white;
                color: $color-blue-p;
                border:1px solid #979797;
            }
        }
        ul{
            text-align: center;
        }
        li +li{
            margin-left: 4.34782609%;
        }
    }
}