.mask{
    height: 48px;
    line-height: 48px;
    background-color: $color-blue-9;
    padding-left: 16px;
}
.previous-page{
    display: inline-block;
    width: 32px;
    height: 32px;
    background: url(../images/previous-page.svg);
    background-size: cover;
    position: relative;
    top: 8px;
    margin-right: 16px;
    cursor: pointer;
}
.layer-font{
    font-size: 0;
    display: inline-block;
    li{
        display: inline-block;
        @include fontstyle($f16,$fw9,$color-black-2);
        cursor: pointer;
        margin-right: 8px;
        position: relative;
        .showbox{
            width: 320px;
            max-height: 240px;
            box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.35);
            overflow: visible;
            position: absolute;
            top:46px;
            left: 10px;
            transform: translateX(-50%);
            border-radius: 4px;
            z-index: 3;
             .img:after{
                left: 8px;
            }
            ul{
                width: 320px;
                max-height: 240px;
                overflow-x: auto;
                border-radius: 4px;
                background-color:$color-white; 
            }
            li{
                display: block;
                width:304px;
                height: 40px;
                border-bottom: 1px solid $color-gray-d2;
                margin-right: 0;
                padding-left: 40px;
            }
            li +li:before{ 
                display: none;
            }
            &:before{
                content: '';
                width: 0;
                height: 0;
                border-style: solid;
                border-width: 7px;
                border-color: #fff #fff transparent transparent;
                transform: rotate(-45deg);
                box-shadow: 1px -1px 1px rgba(0, 0, 0, 0.07);
                position: absolute;
                top:-7px;
                left:50%;
        
            }
            .bgcolor-blue{
                width: 320px;
                background-color: $color-blue-4;
                color: $color-white;
            }
        }
    }
    // li + li::before{
    //     content: '＞';
    //     margin: 0 8px;
    // }
    .fontblue{
        color: $color-blue-p;
    }
    .img{
        position: relative;
        padding-left: 32px;
        &:after{
            content: '';
            width: 24px;
            height: 24px;
            background: url(../images/terminal-group-24.svg);
            background-size: cover;
            position: absolute;
            top:12px;
            left: 0;
        }
    }
}
.wrap-33{
    width: 93%;
    min-width: 312px;
    padding-left: 5.12820513%;
    padding-right: 0;
    position: relative;
    .subject-content.content-block{
        float: none;
        // width: 100%;
        // min-width: 312px;
    }
    .btn_style{
        display: inline-block;
        width: 68px;
        height: 18px;
        line-height: 18px;
        text-align: center;
        @include fontstyle($f12,$fw5,$color-white);
        background-color: $color-orange-f;
        border-radius: 6px;
        position: absolute;
        top:15px;
        right:5.12820513%;
        cursor: pointer;
        &.yellow{
            background-color: $color-yellow-f;
        }
    }
    .subject-content{
        li{
            height: 65px;
        }
    }
}
