.breadcrumbs{
    width: 992px;
    height: 48px;
    background-color: $color-blue-9;
    position: relative;
    .icon{
        width: 32px;
        height: 32px;
        margin-left:16px;
        margin-top: 8px; 
        background-image: url("../images/previous-page.png");
        background-size: cover;
        background-repeat: no-repeat;
        float: left;
    }
    .terminal-group_24{
        width: 24px;
        height: 24px;
        margin-left:16px;
        margin-top: 12px; 
        background-image: url("../images/terminal-group-24.png");
        background-size: cover;
        background-repeat: no-repeat;
        float: left;
    }
    p{
        margin-left:8px;
        margin-right:8px;
        float: left;
        @include fontstyle($f16,$fw5);
    }
    .fontcolor{
        color: $color-blue-p;
    }
    .fontbold{
        font-weight: $fw9;
    }
}