.menu2{
  width: 992px;
  height: 56px;
  box-sizing: border-box;
  padding-left: 24px;
  p{
      @include fontstyle($f12);
      margin-top: 20px;
      margin-bottom: 20px;
      padding: 2px 0;
  }
  .bar{
      width: 120px;
      height: 24px;
      color: $color-gray-a;
      font-weight: $fw5;
      text-align: center;
      float: left;
  }
  .thisbar{
    background-color:$color-blue-p;
    border-radius: 16px;
    color: $color-white;
    margin-top: 16px;
    p{  
        margin-top: 6px;
        margin-bottom: 6px;
        padding: 0px;
    }
  }
  .bar_default{
    width: 138px;
    height: 32px;
    line-height: 32px;;
    border-radius: 16px;
    font-weight: $fw5;
    margin:12px 0; 
    margin-right: 12px;
    margin-left: 82px;
    background-color:$color-gray-a;
    border: 1px solid $color-blue-p;
    text-align: center;
    float: left;
    &.greencolor{
      background-color: $color-green-2;
    }
    p{
        margin:0 0;
        padding: 0px;
        height: 16px;
        text-shadow: 0 1px 5px rgba(0, 0, 0, 0.1);
        color: $color-white;
    }
  }
  .icon{
      float: left;
      .img{
        float: left;
          width: 32px;
          height: 32px;
          margin-top: 12px;
          margin-right: 12px;
      }
      .lock{
        border:1px solid $color-blue-p;
        background-color: $color-gray-a;
        border-radius: 16px;
        background-image: url("../images/123.png");
        background-size: cover;
        background-repeat: no-repeat;
        cursor: pointer;
        &:hover{
          background-color: $color-green-2;
      }
      }
      .copy{
        background-image: url("../images/D463774C-7C49-4896-8ADB-7284041BB92F.png");
        background-size: cover;
        background-repeat: no-repeat;
      }
      .del{
        background-image: url("../images/8EEE30E1-9A53-49F9-9F7F-91D986A566A0.png");
        background-size: cover;
        background-repeat: no-repeat;
      }
  }
}