.pop-up_window{
    font-size: 0;
    width: 586px;
    background-color: $color-white;
    border-radius: 4px;
    position: fixed;
    top:50%;
    left: 50%;
    transform: translate(-50%,-50%);
    z-index: 4;
    box-sizing: border-box;
    h2{
        @include fontstyle($f20,$fw9,$color-gray-32);
        text-align: center;
    }
    h3{
        @include fontstyle($f16,$fw5,$color-black-2);
    }
    .section{
        margin: 18px 0;
        @include fontstyle(0);
        word-spacing:0px;
        text-align: left;
        height: 352px;
        overflow-y: auto;
        white-space: nowrap;

        .section_title{
            width: 100%;
            min-width: 538px;
            word-spacing:0px;
            letter-spacing: 0;
            text-align: left;
            @include fontstyle(0);
            .left{
                border-right:1px solid $color-gray-a;
            }
        }
        h3{
            width: 50%;
            min-width: 268px;
            height: 32px;
            line-height: 32px;
            display: inline-block;
            padding-left: 16px;
            box-sizing: border-box;
            @include fontstyle($f12,$fw5,$color-gray-a);
            border-top:1px solid $color-gray-a;
            border-bottom:1px solid $color-gray-a;
        }
        .section_left,.section_right{
            float: left;
            .click{
                width: 100%;
                background-color: $color-blue-p;
                color:$color-white;
                border-bottom:0; 
                &.shortwidth{
                    width: 100%;
                    min-width: 156px;
                }
            }
        }
        ul{
            width: 50%;
            @include fontstyle($f12);
            &.wp-100{
                width: 100%;
            }
            li{
                padding-left:8px;
                box-sizing: border-box; 
                width: 88.1040892%;
                height: 40px;
                line-height: 40px;
                @include fontstyle($f14,$fw5,$color-gray-4d);
                border-bottom: 1px solid $color-gray-d2;
                cursor: pointer;
            }
        }
        .shortwidth{
            width: 28.9962825%;
            min-width: 156px;
        }
        .longwidth{
            width: 71.0037175%;
            min-width: 382px;
        }
        .block{
            display: inline-block;
            vertical-align: middle;
            width: 24px;
            height: 12px;
            border-radius: 4px;
            background-color: $color-green-2;
            margin-right: 2px;
            &.bg-orange{
                background-color: $color-orange-f;
            }
            &.bg-yellow{
                background-color: $color-yellow-e;
            }
            &.bg-black{
                background-color: $color-gray-3;
            }
            &.bg-gray{
                background-color: $color-gray-c;
            }
        }
        &.n-overflow{
            overflow-x: scroll;
            overflow-y:hidden;
        }
        .select{
            width: 100%;
            background-color: $color-blue-p;
            color:$color-white;
            position: relative;
            &:after{
                content: '';
                height: 16px;
                width: 16px;
                background: url("../images/white-right-arrow.png");
                background-size: cover;
                position: absolute;
                top:12px;
                right:8px;
            }
            &.select-2{
                background-color: $color-gray-c;
                color:$color-gray-4d;
                &:after{
                    content: '';
                    height: 16px;
                    width: 16px;
                    background: url("../images/black-right-arrow.png");
                    background-size: cover;
                    position: absolute;
                    top:12px;
                    right:8px;
                }
            }
        }
        .select-3{
            width: 100%;
            background-color: $color-blue-p;
            color:$color-white;
        }
        .select-4{
          opacity: .3;
          border-color: $color-gray-a;
        }
        ul + ul{
            border-left:1px solid $color-gray-d2;
        }
        .block-one{
            height: 320px;
            overflow-y: auto;
            display: inline-block;
            li{
                width: 100%;
            }
            .click{
                background-color: $color-blue-p;
                color: $color-white;
            }
        }
        .left-side{
            width: 46.2825279%;
            // white-space: nowrap;
            // overflow-x:scroll;
            // overflow-y: hidden; 
            // font-size: 0;
            // ul{
            //     width: 100%;
            // }
        }
        .right-side{
            width: 46.2825279%;
        }
        .center{
            width: 7.43494424%;
            background-color: $color-gray-d0;
            li{
                text-align: center;
                padding-left: 0;
                .dragimg{
                    width: 24px;
                    height: 24px;
                    background-image: url(../images/drag-24.svg);
                    display: inline-block;
                    vertical-align: middle;
                    cursor: pointer;
                    &.left{
                        transform: rotate(180deg);
                    }
                }
            }
        }
        .admin{
            width: 24px;
            height: 24px;
            background-image: url(../images/admin-20.svg);
            background-size: cover;
            display: inline-block;
            vertical-align: middle;
            margin-right: 2px;
        }
        .surper-admin{
            width: 24px;
            height: 24px;
            background-image: url(../images/super-admin-20.svg);
            background-size: cover;
            display: inline-block;
            vertical-align: middle;
            margin-right: 2px;
        }
        .folder-img{
            width: 24px;
            height: 24px;
            background-image: url(../images/terminal-group-24.svg);
            display: inline-block;
            vertical-align: middle;
            margin-right: 2px;
        }
        &.showbox{
            width: 320px;
            height: 240px;
            box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.35);
            overflow: visible;
            position: absolute;
            top:15px;
            left:-110px;
            z-index: 3;
            ul{
                width: 320px;
                height: 240px;
                overflow-y: auto;
                border-radius: 4px;
                background-color:$color-white; 
            }
            li{
                width: 304px;
                font-size: 15px;
                @include fontstyle(15px,$fw5,$color-blue-p);
                display: block;
            }
            li +li:before{ 
                display: none;
            }
            &:before{
                content: '';
                width: 0;
                height: 0;
                border-style: solid;
                border-width: 7px;
                border-color: #fff #fff transparent transparent;
                transform: rotate(-45deg);
                box-shadow: 1px -1px 1px rgba(0, 0, 0, 0.07);
                position: absolute;
                top:-7px;
                left:50%;

            }
            .bgcolor-blue{
                width: 320px;
                background-color: $color-blue-4;
                color: $color-white;
            }
        }
        &.type2{
            ul{
                border-left:0;
                li{
                    width: 100%;
                } 
            }
        }

    }
    .btn-bar{
        display: inline-block;
        vertical-align: middle;
        width: 40%;
        margin: 0;
        &.w100percent{
            width: 100%;
            .bar1{
                margin: 0;
                margin-right: 8px;
            }
        }
        .bar{
            width: 190px;
            .bar1{
                width: 80px;
                margin: 0;
                &.ml-24{
                    margin-left: 24px;
                }
            }
        }
    }
    .shortsearch{
        width: 59.4895539%;
        min-width: 320px;
        display: inline-block;
        vertical-align: middle;
    }
    .title-left{
        ul{
            font-size: 0;
            li{
                position: relative;
                display: inline-block;
                @include fontstyle($f16,$fw9);
                a{
                    @include fontstyle($f16,$fw9,$color-black);
                    &.click{
                        color: $color-blue-p;
                        font-weight: $fw5;
                    }
                }
            }
            li +li:before{
                content: '>';
                margin: 0 8px;
                @include fontstyle($f16,$fw9);
            }
        }
    }
    .clear-btn{
        width: 37.1747212%;
        min-width: 200px;
    }
    &.h-700{
        height: 700px;
        overflow-y: auto;
    }
    .schedule-btn-2{
        width: 56.6914498%;
    }
}
.login-box{
    font-size: 0;
    width: 480px;
    height: 420px;
    background-color: $color-gray-f4;
    border-radius: 4px;
    position: fixed;
    top:50%;
    left: 50%;
    transform: translate(-50%,-50%);
    z-index: 4;
    box-sizing: border-box;
    box-shadow: 1px 1px 4px 0 rgba(0, 0, 0, 0.1);
    .login{
        width: 64.1666667%;
        margin: auto;
        padding:0 5%;
        @include fontstyle($f16,$fw5,$color-black-2);
        &.mt-42{
            margin-top: 42px;
        }
        &.mt-16{
            margin-top: 16px;
        }
        p{
            margin: 0;
        }
        input{
            width: 100%;
            margin-top: 6px;
            border-color: $color-gray-a;
        }
    }
    .login-button{
        width: 53.333333%;
        height: 32px;
        margin: auto;
        border-radius: 4px;
        box-shadow: 0 1px 10px 0 rgba(0, 0, 0, 0.2);
        line-height: 32px;
        background-color: $color-blue-p;
         text-align: center;
          @include fontstyle($f16,$fwb,$color-white);
        &.mt-38{
            margin-top: 38px;
        }
    }
    .errmsg{
        width: 90%;
        height: 18px;
        line-height: 18px;
        margin: auto;
        @include fontstyle($f12,$fwn,$color-red-e);
        text-align: center;
        &.mt-16{
            margin-top: 16px;
        }
        p{
            margin: 0;
        }
    }
}
@include respond-to(medium) {
    .pop-up_window {
        width: 1100px;
    }
    .login-box{
        width: 900px;
    }
}

@include respond-to(large) {
    .pop-up_window {
        width: 1465px;
    }
    .login-box{
        width: 1200px;
    }
}

.checkalert{
    width: 420px;
    border-radius: 6px;
    margin: auto;
    padding: 16px;
    box-sizing: border-box;
    @include fontstyle(0);
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    background-color: $color-white;
    z-index: 4;
    .title{
        background: url("../images/caution-48.png") no-repeat;
        padding-left:64px;
        height: 48px;
        line-height: 48px;
        @include fontstyle($f18,$fw9);
    }
    .content{
        padding-left: 64px;
        @include fontstyle($f16,$fw5);
        margin-bottom: 16px;
    }
    .btn_style{
        text-align: right;
        @include fontstyle(0);
        .button{
            text-align: center;
            width: 76px;
            padding: 4px 0;
            display: inline-block;
            @include fontstyle($f16,$fwb);
            margin-left: 16px;
            &.yes_btn{
                color:$color-red-e;
            }
            &.no_btn{
                color: $color-blue-p;
            }
        }
    }
}
@include respond-to(medium) {
    .checkalert {
        width: 788px;
    }
}

@include respond-to(large) {
    .checkalert {
        width: 1050px;
    }
}
.groupimg{
    width: 423px;
    height: 116px;
    margin: auto;
    img{
        width: 100%;
        height: 100%;
        vertical-align: bottom;
    }
}
.checkgroup{
    font-size: 0;
    text-align: center;
    .smallimg{
        width: 156px;
        height: 88px;
        display: inline-block;
        img{
            width: 100%;
            height: 100%;
            vertical-align: bottom;
        }
    }
    .checkbox-new{
        display: inline-block;
        .radio-style{
            display: block;
        }
    }
}
.black-img-1{
    width: 16px;
    height: 16px;
    background: url("../images/black-down.svg");
    background-size: cover;
    display: inline-block;
    margin-left: 17px;
    vertical-align: middle;
    transform: rotate(90deg);
    cursor: pointer;
    &.imgup{
        transform: rotate(-90deg);
    }
}
.advanced-settings{
    position: relative;
    cursor: pointer;
    font-size: 0;
    p{
        @include fontstyle($f16,$fwb,$color-black-2);
        display: inline-block;
        vertical-align: middle;
        margin: 0;
    }
    .black-img{
        width: 16px;
        height: 16px;
        background: url("../images/black-down.svg");
        background-size: cover;
        display: inline-block;
        margin-left: 17px;
        vertical-align: middle;
        transform: rotate(90deg);
        &.imgup{
            transform: rotate(-90deg);
        }
    }
    .border-b{
        border-bottom:1px solid $color-gray-e6;
        width: 538px; 
        padding-bottom: 8px;
    }
    .addnew-btn{
        width: 15.9851301%;
        min-width: 86px;
    }
}
.showbox-blue{
    width: 336px;
    text-align: center;
    border-radius: 6px;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.35);
    background-image: linear-gradient(242deg,$color-blue-004,$color-blue-002);
    position: fixed;
    top:50%;
    left: 50%;
    transform: translate(-50%,-50%);
    z-index: 5;
    &.w380percent {
        width: 380px;
    }
    &:after{
        content: '';
        width: 16px;
        height: 17px;
        background-image: url('../images/close-white.png');
        background-size: cover;
        position: absolute;
        top:9px;
        right:9px;
        cursor: pointer;
    }
    .title{
        @include fontstyle($f18,$fw9,$color-blue-5);
        height: 34px;
        line-height: 34px;
        background-color: $color-blue-417;
        position: relative;
    }
    .content{
        @include fontstyle($f14,$fwb,$color-yellow-ff);
    }
    .btn{
        width: 240px;
        height: 34px;
        @include fontstyle($f16,$fwb,$color-white);
        background-color: $color-blue-p;
        margin: auto;
        margin-bottom: 26px;
        line-height: 34px;
        border-radius: 4px;
        box-shadow: 1px 1px 4px 0 rgba(0, 0, 0, 0.1);
        text-align: center;
        cursor: pointer;
    }
}
@include respond-to(medium) {
    .showbox-blue {
        width: 472px;
        &.w380percent {
            width: 535px;
        }
    }
}

@include respond-to(large) {
    .showbox-blue {
        width: 630px;
        &.w380percent {
            width: 712px;
        }
    }
}