.icon-modify{
    font-size: 0;
    height: 56px;
    .icon{
        display: inline-block;
        cursor: pointer;
    }
    .font{
        @include fontstyle($f12,$fwb,$color-white);
        padding: 8px 12px;
        background-color: $color-gray-a;
        border: 1px solid $color-blue-p;
        border-radius: 16px;
        margin-right: 12px;
        position: relative;
        top:-12px;
        line-height: 1;
        &:hover{
            background-color: $color-green-2;
            color: $color-white;
        }
    }
    .lock{
        width: 32px;
        height: 32px;
        background: url(../images/lock-nor.svg);
        background-size: cover;
        &:hover{
            background: url(../images/unlock-nor.svg);
        }
    }
    .copy{
        width: 32px;
        height: 32px;
        background: url(../images/copy-terminal-settings-nor.svg);
        background-size: cover;
    }
    .del{
        width: 32px;
        height: 32px;
        background: url(../images/delete-32-nor.svg);
        background-size: cover;
    }
    .close{
        width: 32px;
        height: 32px;
        background: url(../images/close-detail-sheet-32-nor.svg);
        background-size: cover;
        margin-top: 3px;
    }
    .back{
        width: 40px;
        height: 40px;
        background: url(../images/circle-32-nor.svg);
        background-size: cover;
        cursor: pointer;
    }
    .black-16{
        width: 16px;
        height: 16px;
        background: url(../images/black-16.svg);
        background-size: cover;
    &.right{
        .font{
            top:0;
        }
        .icon{
            vertical-align: middle;
        }
        .back{
            width: 32px;
            height: 32px;
            background: url(../images/circle-32-nor.svg);
            background-size: cover;
            cursor: pointer;
        }
        .black-16{
            width: 16px;
            height: 16px;
            background: url(../images/black-16.svg);
            background-size: cover;
            cursor: pointer;
        }
      }
    }
}