.bg_left-2{
    background-image: linear-gradient(to bottom, rgba(0, 85, 150, 0.35), $color-blue-p);
    background-color:transparent ;
}
.logo-img{
    width: 236px;
    height: 48px;
    margin:24px auto;
    &.mt-40{
        margin-bottom: 0;
        margin-top: 40px;
    }
}
.rectangle01 .title-w{
    @include fontstyle($f24,$fw9,$color-white);
}
.bor-btm-2{
    width: 100%;
    height: 1px;
    background-image: linear-gradient(to right,$color-gray-a,$color-gray-c);
}
.rectangle-mask{
    min-width: 660px;
    height: 468px;
    padding: 0 3.63636364%;
    overflow-x: auto;
    border-radius: 4px;
    box-shadow: inset 0 1px 5px 0 rgba(0, 0, 0, 0.35);
    border: solid 1px $color-blue-p;
    background-color: rgba(255, 255, 255, 0.35);
    .title-1{
        @include fontstyle($f18,$fwb,$color-black-2);
    }
 
    .container{
        margin: 0;
        padding: 0;
        width: 100%;
        font-size: $f14;
        color: $color-black-2;
        p{
            margin: 0;
        }
        .bold{
            font-weight: $fwb;
            line-height: 24px;
            &.center{
                text-align: center;
            }
        }
        .regular{
            font-weight:$fwn;
            line-height: 24px;
        }
    }
}
.rectangle01 .wrap01 .input-style{
    &.input-style-3{
        input{
            min-width: 192px;
            width: 29.0909091%;
        }
        .w-232{
            min-width:232px;
            width: 35.1515152%;
        }
    }
}
.rectangle01 .wrap01{ 
    .registration{
        width: 210px;
        height: 210px;
        position: absolute;
        right: 24px;
        bottom: 24px;
        background-image: url("../images/registration.svg");
        background-size: cover;
        background-repeat: no-repeat;  
    }
    .licensed{
        width: 177px;
        height: 210px;
        position: absolute;
        right: 40.5px;
        bottom: 24px;
        // background-image: url("../images/licensed.svg");
        background-size: cover;
        background-repeat: no-repeat;  
    }
    .list-name{
        margin: 0 3.636363%;
        margin-top: 8px;
        p{
            margin: 0;
            margin-top: 16px;
            @include fontstyle($f14,$fwn,$color-gray-a); 
        }
        .msg{
            margin-top: 6px;
            @include fontstyle($f18,$fwb,$color-blue-0); 
        }
    }
    .setup{
        width: 222px;
        height: 210px;
        position: absolute;
        right: 24px;
        bottom: 24px;
        background-image: url("../images/setup.svg");
        background-size: cover;
        background-repeat: no-repeat;  
    }
    .rectangle-copy{
        min-width: 380px;
        width: 57.5757576%;
        height: 68px;
        margin-left: 3.63636364%;
        border-radius: 6px;
        box-shadow: 1px 1px 4px 0 rgba(0, 0, 0, 0.1);
        background-color: #ffffff;
        &.mt-24{
            margin-top: 24px;
        }
        h3{
            line-height: 68px;
            padding-top: 0;
        }
        .nexticorn{
            top: 18px;
        }
    }
}
.msgstatus-2{
    background-color: $color-yellow-f;
    color: $color-red-e;
}
.rectangle01 .wrap01 .input-style .msg-2{
    min-width: 192px;
    width: 29.0909091%;
}
.rectangle01 .wrap01 .input-style .msg-3{
    min-width:232px;
    width: 35.1515152%;
}
.groupbox .files2{
    .surper-admin{
        width: 38px;
        height: 38px;
        position: absolute;
        background-image: url(../images/super-admin-white-20.svg);
        background-size: cover;
        background-repeat: no-repeat;
        bottom: 5px;
        right: 5px;
        opacity:0.3;
    }
    .admin{
        width: 38px;
        height: 38px;
        position: absolute;
        background-image: url(../images/admin-20.svg);
        background-size: cover;
        background-repeat: no-repeat;
        bottom: 5px;
        right: 5px;
        opacity:0.3;
    }
}