
.checkbox-new{
    text-align: left;
    margin-bottom: 16px;
    font-size: 0;
    &.mb-0{
      margin-bottom: 0;
    }
    .title{
        text-align: left;
        @include fontstyle($f16,$fw5,$color-black-2);
        margin-bottom: 13px;
        &.t-r{
          text-align: left;
        }
    }
    label{
       margin-bottom: 0;
        display: block;
        height: 40px;
        line-height: 40px;
        @include fontstyle($f16,$fw5,$color-black-2);
       &.mb-32{
         margin-bottom: 32px;
       }
       .w-140{
         width: 23.890785%;
       }
       
    } 
    .terminal-option{
      height: auto;
      .terminal-select{
        padding-left: 6.06060606%;
      }
    }
    .subject{
          @include fontstyle($f12,$fw5,$color-black-2);
          margin: 0;
          padding-left: 40px;
        }
    .checkbox-style {
        display: inline-block;
        -webkit-appearance: none;
        -moz-appearance: none;
        -ms-appearance: none;
        -o-appearance: none;
        appearance: none;
        position: relative;
        top: 6px;
        right: 0;
        bottom: 0;
        left: 0;
        height: 24px;
        width: 24px;
        background: $color-white;
        border: 1px solid $color-gray-a;
        border-radius: 2px;
        color: #fff;
        cursor: pointer;
        z-index: 1;
        margin-right: 10px;
      }
      .checkbox-style:checked {
        background: $color-blue-p;
        border: 1px solid $color-blue-p;
      }
      .checkbox-style:checked::before {
        position: absolute;
        content: '✔';
        display: inline-block;
        font-size: 18px;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
      }
      .radio-style{
        display: inline-block;
        width: 150px;
        &.w-180{
          width: 180px;
        }
    }
    .radio{
        display: inline-block;
        width: 24px;
        height: 24px;
        vertical-align: middle;
        margin: 0;
        margin-top: -5px;
        margin-right: 5px;
        position: relative;
        -webkit-appearance: none;
        -moz-appearance: none;
        -ms-appearance: none;
        -o-appearance: none;
        appearance: none;
        background-color: $color-white;
        border:1px solid $color-gray-a;
        border-radius: 50%;
        cursor: pointer;
        &:checked::before {
            content: '';
            display: block;
            height: 20px;
            width: 20px;
            background-color: $color-blue-p;
            border-radius: 50%;
            position: absolute;
            top:50%;
            left:50%;
            transform: translate(-50%,-50%);
          }
    }
  }
  .checkbox-new-2{
    label{
      height: 32px;
      line-height: 32px;
    }
    .checkbox-style{
      width: 16px;
      height: 16px;
      top: 2px;
    }
    .checkbox-style:checked::before{
    font-size: $f12;
    }
  } 
  .checkbox-new-3{
    padding: 0 3.636363%;
    &.pt-33{
      padding-top: 33px;
    }
    label{
      margin-bottom: 8px;
       display: block;
       @include fontstyle($f16,$fw5,$color-black-2);
       line-height: 1;
       .maximum{
         padding-left:38px;
        font-size:$f12;
        font-weight: 300;
        color: $color-gray-a;
        font-style: italic;
       }
   }
