input{
    // width: 100%;
    height: 32px;
    border-radius: 4px;
    background-color: $color-white;
    border: 1px solid $color-gray-e;
    padding-left: 8px;
    @include fontstyle($f12,$fw5,$color-black-2);
}
.input-style2{
        
        @include fontstyle($f12,$fw5,$color-black-2);
        appearance: none;
        -moz-appearance: none;
        -webkit-appearance: none;
        border: 1px solid $color-gray-b;
        background-color: $color-white;
        background: url("../images/select.svg") no-repeat scroll right center transparent;
        background-size: 16px;
        background-position: right 8px top 50%;   //位置可設定//
        padding-left: 8px;
        &.gray-italic{
            font-style: italic;
            color: $color-gray-a;
        
    }

}

.input-style3{
    background: url("../images/show-pw.svg") no-repeat scroll right center transparent;
    background-size: 32px;
    background-position: right 8px top 50%;   //位置可設定//
    }
.input-style4{
        background: url("../images/hide-pw.svg") no-repeat scroll right center transparent;
        background-size: 32px;
        background-position: right 8px top 50%;   //位置可設定//
}
.input-style{
    letter-spacing: 0;
     word-spacing: 0; 
    .fontstyle-16{
        @include fontstyle($f16,$fw5,$color-black-2);
        span{
            padding-left: 8px;
            @include fontstyle($f16,$fwn,$color-gray-a);
        }
    }
    .fs-12{
        font-size:$f12;
    }
    .w82percent{
        width:82.1561338% ;
    }
    .addnew-btn{
        width: 72px;
        &.ml-24{
            margin-left: 4.46096654%
        }
    }
    .w82percent,.addnew-btn{
        display: inline-block;
    }
    label{
        margin-bottom: 0;
        display: block;
    }
    textarea{
        border:1px solid $color-gray-a;
        border-radius: 4px;
        resize: none;
    }
    .br-color{
        border-color: $color-gray-a;
    }
    select{    
        @include fontstyle($f12,$fw5,$color-black-2);
        appearance: none;
        -moz-appearance: none;
        -webkit-appearance: none;
        border: 1px solid $color-gray-b;
        border-radius: 4px;
        background-color: $color-white;
        background: url("../images/dropdown-24.svg") no-repeat scroll right center transparent;
        background-size: 24px;
        padding-left: 8px;
        &.gray-italic{
            font-style: italic;
            color: $color-gray-a;
        }
    }
    .w8percent{
        width: 8%;
    }
    .w120percent{
        width: 22.3048327%;
    }
    .w162percent{
        width: 30.1115242%;
    }
    .w240percent{
        width: 44.6096654%;
    }
    .databtn{
        margin-top: 5px;
        @include fontstyle(0);
        word-spacing:0px;
        .databtn-1{
            display: inline-block;
            @include fontstyle($f12,$fw5,$color-white);
            margin-right: 10px;
            width: 32px;
            height: 32px;
            line-height: 32px;
            text-align: center;
            border-radius: 50%;
            background-color: $color-blue-p;
            cursor: pointer;
            &.databtn-2{
                color:$color-blue-p;
                background-color: $color-white;
                border:1px solid $color-gray-9;
            }
        }
    }
    .msg{
           margin: 0;
           text-align: right;
           @include fontstyle($f10, $fwn,$color-blue-p);
       }
       .red-input{
        color:$color-red-e ;
        border-color: $color-red-e;
    }
    .err{
        color: $color-red-e;
   }
}

.search{
    position: relative;
    .searchabr{
        border-radius: 16px;
        padding-left: 30px;
        box-sizing: border-box;
        &.gray-italic{
            font-style: italic;
            color: $color-gray-a;
        }
    }
    .fa-search{
        color: $color-gray-8e;
        font-size: $f16;
        position: absolute;
        top:8px;
        left: 8px;
    }
}
.maximun-charactor{
    @include fontstyle($f12,$fw5,$color-gray-a);
    white-space: nowrap;
    margin: 0;
    word-spacing: 0;
    letter-spacing: 0;
}



