.app_rsd{
    font-size: 0;
    padding-bottom: 16px;
    letter-spacing: 0;
    word-spacing: 0;
    h3{
        @include fontstyle($f12,$fw5,$color-gray-a);
    }
    .left-side{
        display: inline-block;
        vertical-align: top;
        width: 45.2586207%;
        .top{
            height: 315px;
            padding-top: 16px;
            background-image: linear-gradient(to left, $color-gray-7, $color-gray-4f);
        }
        .bottom{
            height: 128px;
            background-color: #E5E9EB;
            padding: 24px 6.19047619%;
            .circle{
                display: inline-block;
                vertical-align: top;
                width: 32px;
                height: 32px;
                text-align: center;
                line-height: 32px;
                border-radius: 50%;
                @include fontstyle($f14,$fw5,$color-white);
                background-color: $color-gray-d2;
                cursor: pointer;
                &.plusimg{
                    position: relative;
                    &:after{
                        content: '';
                        width: 40px;
                        height: 40px;
                        background-image: url("../images/plus.svg");
                        background-size: cover;
                        background-repeat: no-repeat;
                        position: absolute;
                        top:-3px;
                        left: -3px;
                    }
                }
                &.bgc-blue{
                    background-color: $color-blue-p;
                }
            }
            ul{
                text-align: center;
            }
            li +li{
                margin-left: 4.34782609%;
            }
        }
    }
    .center-box{
        display: inline-block;
        vertical-align: top;
        width: 25.9698276%;
        height: 443px;
        padding-left: 2.587%;
        padding-right: 1.725%;
        .img-line{
            text-align: center;
            margin-top: 19px;
            margin-bottom: 24px;
            .ml-16{
                margin-left: 8%;
            }
            .up{
                display: inline-block;
                width: 37px;
                height: 37px;
                background-image: url("../images/up-32.svg");
                background-size: cover;
                cursor: pointer;
            }
            .down{
                display: inline-block;
                width: 37px;
                height: 37px;
                background-image: url("../images/down-32.svg");
                background-size: cover;
                cursor: pointer;
            }
            .system{
                display: inline-block;
                width: 37px;
                height: 37px;
                background-image: url("../images/system-32.svg");
                background-size: cover;
                cursor: pointer;
            }
            .del{
                display: inline-block;
                width: 37px;
                height: 37px;
                background-image: url("../images/delete-32.svg");
                background-size: cover;
                cursor: pointer;
            }
        }
        ul{
            li{
                .font{
                    display: inline-block;
                    width: 38%;
                    @include fontstyle($f12,$fw5,$color-gray-a);

                }
                .count-btn{
                    width: 62%;
                    text-align: right;
                    display: inline-block;
                }
            }
        }
    }
    .right-side{
        display: inline-block;
        vertical-align: top;
        width: 28.7715517%;
        height: 443px;
        padding-left: 1.40086207%;
        .checkbox-new{
            .radio-style{
                width: unset;
                @include fontstyle($f12,$fw5,$color-gray-4);
                &.ml-12{
                    margin-left: 4.72440945%;
                }
            }
        }
        .scroll{
            height: 305px;
            overflow-y: auto;
            .schedule-btn-2{
                width: 61.4173228%;
                min-width: unset;
                padding-left: 11.71875%;
                padding-right: 0;
                margin-bottom: 8px;
            }

        }
    }
    .show-pic{
        width: 205px;
        height: 208px;
        background-color: $color-white;
        border:3px solid $color-blue-p;
        border-radius: 4px;
        margin:0 auto;
        position: relative;
        .triangle{
            position: absolute;
            width: 0;
            height: 0;
            border-style: solid;
            border-width: 48px 48px 0 0;
            border-color: $color-blue-p transparent transparent transparent;
            p{
                width: 20px;
                height: 20px;
                text-align: center;
                position: absolute;
                top:-47px;
                left: 2px;
                @include fontstyle($f14,$fw5,$color-white);
                margin: 0;
            }
        }
        .pic{
            width: 199px;
            height: 133px;
            margin-top: 38px;
            img{
                width: 100%;
                height: 100%;
                vertical-align: bottom;
            }
        }
    }
}