@mixin icon_circle(){
    align-items: center;
    padding: 20px;
    margin-bottom: 0;
    font-size: 15px;
    font-weight: 400;
    color: #5c6873;
    text-align: center;
    white-space: nowrap;
    background-color: #f0f3f5;
    border: 1px solid #e4e7ea;
    border-radius: 30px;
    line-height: 1;
}