@mixin fontstyle($size ,$weight:null ,$color:null){
    font-size: $size;
    font-weight: $weight;
    color: $color;
}


@mixin mt ($num) {
    @for $i from 1 through $num {
        .mt-#{$i} {
            margin-top: $i * 1px;
        }
    }
}
@include mt(1000);

@mixin ml ($num) {
    @for $i from 1 through $num {
        .ml-#{$i} {
